import React, { useState, useEffect } from "react";
import ReactDOM from 'react-dom'
import Persons from './Persons'
import AddPerson from './AddPerson'
import Filter from './Filter'
import personService from './services/persons'
import Notification from './Notification'
import './styles/index.css'
/* json-server --watch db.json --port 3001 */
const App = () => {

  

  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState("")
  const [newNumber, setNewNumber] = useState("")
  const [newFilter, setNewFilter] = useState("")
  const [errorMessage, setErrorMessage] = useState(null)



 const hook = () => {
  console.log('effect')
  personService
    .getAll()
    .then(response=>{
      setPersons(response.data)
    })
}
  useEffect(hook, [])
  
  
  console.log('render',persons.length, 'persons');


  const addPerson = (event) => {
    event.preventDefault()
    const personObject = {
      name: newName,
      number: newNumber
    }

    if(persons.some(e=> e.name.toLowerCase() === newName.toLowerCase())){ 
      console.log(newName,persons)
      const result = persons.filter(person => {
        return person.name === newName
      })
      const id = result[0].id
      updatePerson({personObject, id});
    }

    else {
    personService
      .create(personObject)
      .then(response => {
        setPersons(response.data)
        console.log(response.data)
        setNewName('')
        setNewNumber('')
        setErrorMessage(
          `You have added ${personObject.name} to the list !`
        )
        setTimeout(() => {
          setErrorMessage(null)
        },3000)
      })
    }
  }
  const updatePerson = ({personObject, id}) => {
    if(window.confirm("Person is already added to phonebook, replace the old number with a new one?")){
    personService
      .update(id, personObject)
      .then(response =>{
        console.log(response.data)
        personService
        .getAll()
        .then(response=>{
          setPersons(response.data)
        })
      })
      .catch(error => {     
        setErrorMessage(
          `${personObject.name} Was Already Removed from the server`
        )
      setTimeout(() => {
        setErrorMessage(null)
      },5000)
    },
    setErrorMessage(
      `You have updated ${personObject.name} information`
    ),
    setTimeout(() => {
      setErrorMessage(null)
    },5000)
    )}
    else{
      window.alert("You decided to keep the old data")
    }
  }
  const removePerson = ({id}) => {
    console.log("Button Clickeeed")
    console.log({id})
    personService
      .remove(id)
      .then(response =>{
        console.log(response.data)
        personService
        .getAll()
        .then(response=>{
          setPersons(response.data)
        })
      })
      setErrorMessage(
        `You have removed a person from the list !`
      )
      setTimeout(() => {
        setErrorMessage(null)
      },5000)
      
  }
  
  const handleNewName = (event) => {
    setNewName(event.target.value)
  }

  const handleNewNumber = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    setNewFilter(event.target.value)
  }

  return (
    <div>
      <Notification message={errorMessage}/>
      <h2>Phonebook</h2>
      <Filter filterValue={newFilter} handleChange={handleFilterChange}/>
      <AddPerson 
      addPerson = {addPerson}
      newName = {newName}
      handleNewName = {handleNewName}
      newNumber = {newNumber}
      handleNewNumber = {handleNewNumber}
      />
      <h2>Numbers</h2>
      <Persons
       persons={persons}
       filter={newFilter}
       removePerson={removePerson}
       />
    </div>
  );
};

ReactDOM.render(
  <App />,
  document.getElementById('root')
)
